h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

textarea {
  resize: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

label {
  margin: 0;
}

.list {
  color: inherit;
  list-style-type: none;
}

body {
  color: var(--main-color);
  background-color: var(--color-white);
  letter-spacing: .03em;
  font-family: Roboto;
}

body.modal-open {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

